}
body.is-menu-open {
  overflow: hidden;
}
@media (min-width: 1200px) {
  body.is-menu-open {
    margin-right: 17px;
  }
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}
a:hover {
  text-decoration: underline;
}

input,
button,
select,
textarea {
  font-weight: normal;
  font-family: "IBM Plex Sans", Arial, sans-serif;
}

input,
button {
  appearance: none;
}

picture,
time {
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6,
.title-size-1,
.title-size-2,
.title-size-3,
.title-size-4,
.title-size-5,
.title-size-6 {
