  line-height: 1.3;
}

h1,
.title-size-1 {
  font-size: var(--fs-1);
}

h2,
.title-size-2 {
  font-size: var(--fs-2);
}

h3,
.title-size-3 {
  font-size: var(--fs-3);
}

h4,
.title-size-4 {
  font-size: var(--fs-4);
}

h5,
.title-size-5 {
  font-size: var(--fs-5);
}

h6,
.title-size-6 {
  font-size: var(--fs-6);
}

strong,
b {
  font-weight: bold;
}

#root {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: clip;
}

.row {
  margin: 0 var(--gap-);
  display: flex;
  flex-wrap: wrap;
}
.row__col, [class^=row__col] {
  max-width: 100%;
  min-height: 1px;
  padding: 0 var(--gap);
  flex-grow: 1;
}
.row__col--2 {
  max-width: 16.6666666667%;
  flex-basis: 16.6666666667%;
}
.row__col--3 {
  max-width: 25%;
  flex-basis: 25%;
}
.row__col--4 {
  max-width: 33.33333333%;
  flex-basis: 33.33333333%;
}
.row__col--5 {
  max-width: 41.6666666667%;
  flex-basis: 41.6666666667%;
}
.row__col--6 {
  max-width: 50%;
  flex-basis: 50%;
}
.row__col--7 {
  max-width: 58.3333333333%;
  flex-basis: 58.3333333333%;
}
.row__col--9 {
  max-width: 75%;
  flex-basis: 75%;
}
.row__col--12 {
  max-width: 100%;
  flex-basis: 100%;
}

svg,
.icon {
  width: 1em;
  height: 1em;
  display: inline-block;
  color: inherit;
  fill: currentColor;
}

.container {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 var(--gap);
}

.container-fluid {
  width: 100%;
  padding: 0 var(--gap-fluid);
}
@media (max-width: 1199px) {
  .container-fluid {
    padding-right: var(--gap);
    padding-left: var(--gap);
  }
}

.img-cover {
  display: block;
  position: relative;
  overflow: hidden;
}
.img-cover img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
}

.img-contain, .appeal-card__picture {
  display: block;
  position: relative;
  overflow: hidden;
}
.img-contain img, .appeal-card__picture img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: contain;
}

.area-click, .btn-add, .badge-info__btn {
  --indent: -5px;
  position: relative;
}
.area-click:before, .btn-add:before, .badge-info__btn:before {
  content: "";
  position: absolute;
  top: var(--indent);
  right: var(--indent);
  bottom: var(--indent);
  left: var(--indent);
}

.link-cover:before, .appeal-card__link:before {
  width: 100%;
  height: 100%;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
}

.btn-reset, .btn-add, .badge-info__btn, .btn-action {
  background: transparent;
  margin: 0;
  padding: 0;
  font-size: 0;
  outline: none;
  border: none;
  border-radius: 0;
}

.text-overflow, .test-result__header-info-link {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.visually-hidden {
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  padding: 0 !important;
  position: absolute !important;
  border: 0 !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  overflow: hidden !important;
}

.u-input {
  width: 100%;
  min-height: var(--input-height);
  padding: var(--input-padding-y) var(--input-padding-x);
  display: block;
  font-weight: 500;
  font-size: var(--input-font-size);
  line-height: 1.35;
  border: none;
  border-radius: 0;
  outline: none;
  transition: all 0.4s;
}
.u-input:hover {
  text-decoration: none;
}
.u-input--theme-white {
  background: transparent;
  color: #fff;
  border: solid #5D6573;
  border-width: 0 0 1px;
}
.u-input--theme-white:hover, .u-input--theme-white:focus {
  border-color: #fff;
}
.u-input--size-md {
  --input-height: 50px;
  --input-padding-y: 8px;
  --input-padding-x: 0;
  --input-font-size: 14px;
}
.u-input--size-sm {
  --input-height: 40px;
  --input-padding-y: 8px;
  --input-padding-x: 0;
  --input-font-size: 14px;
}

.u-label {
  margin: 0;
  padding: 0;
  font-weight: 500;
  text-transform: uppercase;
  display: block;
}

.btn {
  max-width: 100%;
  min-height: var(--btn-height);
  padding: var(--btn-padding-y) var(--btn-padding-x);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-style: normal;
  font-weight: 500;
  font-size: inherit;
  font-size: var(--btn-font-size);
  line-height: 1.3;
  text-align: center;
  text-transform: uppercase;
  border: none;
  border-radius: 0;
  outline: none;
  user-select: none;
  transition: all 0.4s;
  position: relative;
  cursor: pointer;
  overflow: hidden;
  z-index: 0;
}
.btn:hover {
  text-decoration: none;
}
.btn.disabled, .btn:disabled {
  opacity: 0.6;
  pointer-events: none;
}
.btn--theme-accent-border {
  background: transparent;
  color: #fff;
  border-radius: 4px;
}
.btn--theme-accent-border:before, .btn--theme-accent-border:after {
  width: 100%;
  height: 100%;
  content: "";
  border-radius: inherit;
  position: absolute;
  top: 0;
  left: 0;
  transition: all 0.4s;
}
.btn--theme-accent-border:before {
  background: linear-gradient(270deg, #D045AE, #473CC2);
  background-size: 100% 100%;
  clip-path: polygon(0% 100%, 2px 100%, 2px 2px, calc(100% - 2px) 2px, calc(100% - 2px) calc(100% - 2px), 2px calc(100% - 2px), 2px 100%, 100% 100%, 100% 0%, 0% 0%);
  z-index: -2;
}
.btn--theme-accent-border:after {
  background: linear-gradient(270deg, #D045AE, #473CC2);
  opacity: 0;
  visibility: hidden;
  z-index: -1;
}
@media (min-width: 1200px) {
  .btn--theme-accent-border:hover:before {
    opacity: 0;
    visibility: hidden;
  }
}
@media (min-width: 1200px) {
  .btn--theme-accent-border:hover:after {
    opacity: 1;
    visibility: visible;
  }
}
.btn--theme-accent-fill {
  background: linear-gradient(84deg, #473cc2 0%, #d045ae 100%);
  color: #fff;
  border-radius: 4px;
}
.btn--theme-accent-fill:hover {
  text-decoration: none;
}
@media (min-width: 1200px) {
  .btn--theme-accent-fill:not(:hover):before {
    opacity: 0;
    visibility: hidden;
  }
}
.btn--theme-accent-fill:before {
  background: #1B1F26;
  content: "";
  position: absolute;
  top: 2px;
  right: 2px;
  bottom: 2px;
  left: 2px;
  border-radius: inherit;
  transition: all 0.4s;
  z-index: -1;
}
.btn--size-sm {
  --btn-height: 40px;
  --btn-padding-y: 6px;
  --btn-padding-x: 24px;
  --btn-font-size: 14px;
}
.btn--size-md {
  --btn-height: 50px;
  --btn-padding-y: 8px;
  --btn-padding-x: 32px;
  --btn-font-size: 14px;
}
.btn--size-lg {
  --btn-height: 60px;
  --btn-padding-y: 12px;
  --btn-padding-x: 32px;
  --btn-font-size: 14px;
}
.btn--w-100 {
  width: 100%;
}
.btn__inner {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
}
.btn__title {
  padding-top: 0.1em;
}
.btn__icon {
  width: 1.1428571429em;
  height: 1.1428571429em;
  flex-shrink: 0;
  transition: all 0.4s;
  transition-property: transform;
}
.btn__icon--dir-ltr {
  margin-left: 0.5714285714em;
}
.btn__icon--dir-rtl {
  margin-right: 0.5714285714em;
}

.btn-action {
  width: var(--btn-action-size);
  height: var(--btn-action-size);
  display: inline-block;
  font-size: 0;
  position: relative;
  transition: all 0.4s;
  cursor: pointer;
  overflow: hidden;
  z-index: 0;
}
.btn-action--theme-gray {
  background: #21262E;
  color: #5D6573;
  border-radius: 4px;
}
@media (min-width: 1200px) {
  .btn-action--theme-gray:hover {
    background: #2C323D;
    color: #fff;
  }
}
.btn-action--theme-accent-border {
  color: #fff;
  border-radius: 4px;
}
.btn-action--theme-accent-border:before, .btn-action--theme-accent-border:after {
  width: 100%;
  height: 100%;
  content: "";
  border-radius: inherit;
  position: absolute;
  top: 0;
  left: 0;
  transition: all 0.4s;
}
.btn-action--theme-accent-border:before {
  background: linear-gradient(270deg, #D045AE, #473CC2);
  background-size: 100% 100%;
  clip-path: polygon(0% 100%, 2px 100%, 2px 2px, calc(100% - 2px) 2px, calc(100% - 2px) calc(100% - 2px), 2px calc(100% - 2px), 2px 100%, 100% 100%, 100% 0%, 0% 0%);
  z-index: -2;
}
.btn-action--theme-accent-border:after {
  background: linear-gradient(270deg, #D045AE, #473CC2);
  opacity: 0;
  visibility: hidden;
  z-index: -1;
}
@media (min-width: 1200px) {
  .btn-action--theme-accent-border:hover:before {
    opacity: 0;
    visibility: hidden;
  }
}
@media (min-width: 1200px) {
  .btn-action--theme-accent-border:hover:after {
    opacity: 1;
    visibility: visible;
  }
}
.btn-action--size-md {
  --btn-action-size: 50px;
}
.btn-action--size-sm {
  --btn-action-size: 40px;
}
.btn-action__inner {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.btn-action__icon {
  width: 64%;
  height: 64%;
  margin: -32% 0 0 -32%;
  position: absolute;
  top: 50%;
  left: 50%;
}

.nav-link {
  padding: 10px 12px;
  display: flex;
  align-items: center;
  border-radius: 4px;
  text-transform: uppercase;
  position: relative;
}
.nav-link:hover {
  text-decoration: none;
}
.nav-link:not(:hover):before {
  transform: scale(1.2);
  opacity: 0;
  visibility: hidden;
}
.nav-link--is-active:not(:hover):before {
  transform: none;
  opacity: 1;
  visibility: visible;
}
.nav-link:before, .nav-link:after {
  width: 100%;
  height: 100%;
  content: "";
  border-radius: inherit;
  position: absolute;
  top: 0;
  left: 0;
  transition: all 0.32s ease;
  transition-property: transform, opacity, visibility;
}
.nav-link:before {
  z-index: -1;
}
.nav-link:after {
  z-index: -2;
}
.nav-link--theme-gray {
  color: #5D6573;
}
.nav-link--theme-gray.nav-link--is-active {
  color: #fff;
}
.nav-link--theme-gray:before {
  background: linear-gradient(84deg, rgba(71, 60, 194, 0.12) 0%, rgba(208, 69, 174, 0.12) 100%);
}
.nav-link--theme-gray:after {
  background: linear-gradient(90deg, rgba(33, 38, 46, 0) 0%, #21262E 100%);
}
.nav-link--theme-gray sup {
  color: #fff;
}
.nav-link__icon-wrap {
  margin-right: 0.5714285714em;
  flex-shrink: 0;
}
.nav-link__icon {
  width: 2.2857142857em;
  height: 2.2857142857em;
  display: block;
  transition: all 0.4s;
  transition-property: filter, opacity;
}
.nav-link--theme-gray:not(.nav-link--is-active) .nav-link__icon, .nav-link--theme-gray:not(.nav-link--is-active) .nav-link__title {
  filter: grayscale(1);
  opacity: 0.5;
}
.nav-link:hover .nav-link__icon, .nav-link:hover .nav-link__title {
  filter: none;
  opacity: 1;
}
.nav-link__text {
  display: block;
  font-size: 14px;
}
.nav-link__text sup {
  margin-left: 0.5714285714em;
  flex-shrink: 0;
  font-weight: bold;
  font-size: 78.5%;
  white-space: nowrap;
}
.nav-link__title {
  font-weight: 500;
  font-size: inherit;
  transition: all 0.4s;
  transition-property: filter, opacity;
}

.nav__list {
  margin: -2px 0;
